import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Main from '../views/main.vue'
import ShowShi from '../views/shoushi.vue'
import One from '../views/one.vue'
import Two from '../views/two.vue'
import Three from '../views/three.vue'
import Four from '../views/four.vue'
import Five from '../views/five.vue'
import ErrorVue from '../views/error.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    redirect: '/home/main',
    children: [
      {
        path: '/home/main',
        component: Main
      },
      {
        path: '/home/shoushi',
        component: ShowShi
      },
      {
        path: '/home/one',
        component: One
      },
      {
        path: '/home/two',
        component: Two
      },
      {
        path: '/home/three',
        component: Three
      },
      {
        path: '/home/four',
        component: Four
      },
      {
        path: '/home/five',
        component: Five
      },
      {
        path: '/home/error',
        component: ErrorVue
      },
      {
        path: '/home/six',
        component: () => import('@/views/six.vue')
      }
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
